@import url(https://fonts.googleapis.com/css?family=Poiret+One&display=swap);
@import url(https://use.typekit.net/vgy0ntq.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  margin: 0;
  background-color: #1c110b;
  background-image: url(/Temaoppgave04/static/media/background-pic.4d252405.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
}

h1 {
  font-family: 'Poiret One', cursive;
  color: 	#ccb596;
  letter-spacing: 1rem;
}
.header {
    background-color: #1c110bd0;
    padding: 1rem;
}

.header h1 {
    text-align: center;
    font-family: drunk-cowboy, sans-serif;
    font-weight: 400;
    font-style: normal;
    font-size: 3rem;
}

nav {
    color: #ccb596;
    display: grid;
    grid-auto-flow: column;
    justify-content: center;
    grid-gap: 1rem;
    gap: 1rem;
    padding-top: 1rem;
}

nav a {
    color: #ccb596;
    text-decoration: none;
}

nav a:hover {
    font-weight: 700;
}

/* Styrer bildet av Tina i bakgrunnen*/
main {
    background-image: url(/Temaoppgave04/static/media/tina-test.f2995339.png);
    background-repeat: no-repeat;
    background-size: 40%;
    height: 100vh;
    width: 100vw;
    margin-left: -100px;
}

/* Komponenten som endres */
.pages {
    margin-top: 3rem;
    margin-right: 7rem;
    padding: 1rem;
    background-color: #1c110bd0;
    height: 70vh;
    color: white;
    float: right;
    width: 60vw;
    
}

/* P책 de komponentene som trenger litt mer plass p책 siden mellom linjene */
.space {
    line-height: 2rem;
}

.space p {
    margin-top: 1rem;
}

h2 {
    font-family: 'Montserrat', sans-serif;
    color: 	#ccb596;
    margin-bottom: 2rem;
}

h3 {
    font-family: 'Montserrat', sans-serif;
    color: 	white;
}

p {
    color: white;
    font-family: 'Montserrat', sans-serif;
}

/* En grid med de ansatte */
.employees {
    margin-top: 1rem;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 1rem;
    gap: 1rem;
    height: 50vh;
    overflow: scroll;
}

.employee {
    margin: 1rem;
    width: 200px;
    height: 400px;
    margin-bottom: 2rem;
}

.employee img {
    max-width: 100%;
    border-radius: 50%;
    height: auto;
}

.employee h3 {
    text-align: center;
}

.employee p {
    line-height: 0.4rem;
}

/* En grid med det salongen kan tilby */
.treatment {
    display: grid;
    grid-template-columns: 1fr 1fr;
    padding: 0.5rem 0.5rem 0.5rem 0;
    margin-top: 1rem;
    overflow: scroll;
}

.contact {
    margin-top: 1rem
}

/* Grid over bookingen */
.bookings {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 5rem;
    gap: 5rem;
    overflow: scroll;
    height: 50vh;
}

.bookings input, select {
    width: 30vw;
    padding: 0.3rem;
    margin-bottom: 1rem;
}

.smallText {
    font-size: small;
}

.bookings button {
    padding: 1rem;
}
